@import url(https://fonts.googleapis.com/css2?family=Acme&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Teko:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.global{-webkit-animation:moveInLeft 1s ease-in;animation:moveInLeft 1s ease-in}.global h2{color:orange;text-shadow:2px 1px 3px black}.global .global-list p{color:black;margin:40px;padding:10px !important;border:2px solid darkgrey;border-radius:10px}@-webkit-keyframes moveInLeft{0%{opacity:0;transform:translateX(-50px)}80%{transform:translateX(10px)}100%{opacity:1;transform:translateX(0px)}}@keyframes moveInLeft{0%{opacity:0;transform:translateX(-50px)}80%{transform:translateX(10px)}100%{opacity:1;transform:translateX(0px)}}

.form .input-box{height:30px;padding:0px 15px;cursor:pointer}.form .search-button{height:30px;margin-left:10px;border-radius:4px;color:white;font-weight:bold;background:none;cursor:pointer}.form .search-button:hover{background:white;color:black;transition:0.2s ease}

.country-list .country-data{transition:0.4s ease-in}.country-list .country-data h3{color:orange;text-shadow:2px 1px 3px black}.country-list .country-data p{color:black;margin:40px;padding:150px;border:2px solid darkgrey;border-radius:10px}.country-list .country-data .clear-search{height:30px;margin-left:10px;border-radius:4px;color:white;font-weight:bold;background:none;cursor:pointer;margin-top:10px}.country-list .country-data .clear-search:hover{background:white;color:black;transition:0.2s ease}.country-list .country-data .more-country-details{height:30px;margin-left:10px;border-radius:4px;color:white;font-weight:bold;background:none;cursor:pointer;margin-top:10px}.country-list .country-data .more-country-details:hover{background:white;color:black;transition:0.2s ease}

.country{-webkit-animation:moveInRight 1s ease-in;animation:moveInRight 1s ease-in}.country h2{color:orange;text-shadow:2px 1px 3px black}@-webkit-keyframes moveInRight{0%{opacity:0;transform:translateX(100px)}80%{transform:translateX(-20px)}100%{opacity:1;transform:translateX(0px)}}@keyframes moveInRight{0%{opacity:0;transform:translateX(100px)}80%{transform:translateX(-20px)}100%{opacity:1;transform:translateX(0px)}}

.footer{color:black;display:block;position:relative;top:20px}

.content{position:absolute;top:0;color:#f1f1f1;background-color:rgba(173,173,173,0.5);width:100%;height:100%;display:flex;justify-content:center;align-items:center;-webkit-clip-path:polygon(0% 2%, 100% 0%, 100% 98%, 0% 100%);clip-path:polygon(0% 2%, 100% 0%, 100% 98%, 0% 100%);box-shadow:2px 3px 5px black}.content h1{top:0;padding-left:20px;background:#501b1d;text-align:center;border-radius:5px}.content .header{margin:0px 100px}.content .header h1{font-family:"Acme", sans-serif;margin:10px 0px;padding:0px}.content .header p{font-family:"Teko", sans-serif;font-size:25px;margin:0px;padding:0}.content .header .categories{display:flex;flex-direction:row;flex-wrap:nowrap;justify-content:space-evenly}

.container{position:relative;min-height:300px;max-height:100vh;overflow:hidden;margin-bottom:500px}.container .video{width:100%;height:100%}

* {
  background-image: none;
  overflow-y: hidden;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

